import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import CommentContainer from 'theme/containers/CommentContainer'

const footerQuery = graphql`
  query {
    site {
      siteMetadata {
        comment
      }
    }
  }
`

export default () => (
  <StaticQuery
  query={footerQuery}
  render={data => (
  <CommentContainer>
    <p>
      Questions, Comments, Suggestions?
      <a rel="noopener noreferrer" target="__blank" href={data.site.siteMetadata.comment}>
        Open an Issue
      </a>
    </p>
  </CommentContainer>
      )}
  />
)

